import Chart, { ChartItem } from "chart.js/auto";
import { KOLOR_PARTII, PARTIE, glosy2019, okregi, sondaze, sredniaSondazy } from "./dane";
import { RESZTY } from "./reszty";
import {
  NormalizujWynik,
  PodzialMandatowWOkregu,
  PoparcieWOkregu,
  losowanieWyniku,
} from "./ordynacja";
import { ILE_SYMULACJI, Wynik } from "./typy";
import { WYNIKI_2023 } from "./wyniki_2023";
import { komentarze } from "./komentarz";

const PROCENT_PRZESUNIETYCH = 0.3;

const symulacje = document.createElement("p");
symulacje.setAttribute("class", "symulacje");

let partieString = "";
for (const partia of PARTIE) {
  partieString += `<td>${partia}</td>`;
}
let tabelka = "<table>";
tabelka += `<tr><th colspan="3">Okręg wyborczy</th><th colspan="7">Ilości głosów (na komitety powyżej progu)</th><th colspan="5">Podział mandatów</th><th colspan="5">Głosy powyżej ceny mandatu</th></tr>`;
tabelka += `<tr><th>nr</th><th>Nazwa</th><th>Mandaty</th><th>Suma</th>${partieString}<th>Cena mandatu</th>${partieString}${partieString}`;

tabelka += "</tr>";
const sumaGlosow: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const sumaMandatow: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const altSumaMandatow: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const sumaReszt: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
const altSumaReszt: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
let sumaPrzesunietych = 0;
okregi.forEach((okreg) => {
  const wynikWOkregu = WYNIKI_2023[okreg.nr - 1];
  const podzialMandatow = PodzialMandatowWOkregu(wynikWOkregu, okreg, { log: false, inputWGlosach: true});

  // Alternatywny scenariusz
  const alternatywnyWynik = {
    PIS: wynikWOkregu.PIS,
    KONF: wynikWOkregu.KONF,
  };
  const PARTIE_OPOZYCJI = ["KO", "TD", "LEWICA"];
  const rekomendacje = komentarze[okreg.miasto].rekomendacjePartii || [];
  const PRZESUNIETE_GLOSY = Math.round((wynikWOkregu['KO'] + wynikWOkregu['LEWICA'] + wynikWOkregu['TD']) * PROCENT_PRZESUNIETYCH / 100);
  sumaPrzesunietych += PRZESUNIETE_GLOSY;
  for (const partia of rekomendacje) {
    alternatywnyWynik[partia] =
      wynikWOkregu[partia] - PRZESUNIETE_GLOSY / rekomendacje.length;
  }
  if (rekomendacje.length) {
    for (const partia of PARTIE_OPOZYCJI) {
      // odejmujemy całość lub połowę od wyniku innych rekomendowanych partii
      if (!rekomendacje.includes(partia)) {
        alternatywnyWynik[partia] =
          wynikWOkregu[partia] +
          PRZESUNIETE_GLOSY /
            (PARTIE_OPOZYCJI.length - rekomendacje.length);
      }
    }
  } else {
    for (const partia of ["KO", "TD", "LEWICA"]) {
      alternatywnyWynik[partia] = wynikWOkregu[partia];
    }
  }

  const alternatywnyPodzialMandatow = PodzialMandatowWOkregu(
    alternatywnyWynik,
    okreg,
    { log: false, inputWGlosach: true }
  );
  
  // Tabelka
  const glosy = Object.values(wynikWOkregu).reduce(
    (accumulator: number, currentValue: number): number => {
      return accumulator + currentValue;
    },
    0
  );
  const resztaFn = (glosy) =>
    Math.round((glosy + 1) % (podzialMandatow.cenaMandatu)) - 1;

  tabelka += `<tr><td>${okreg.nr}</td><td>${okreg.miasto}</td><td>${okreg.mandaty}</td><td>${glosy}</td>`;
  for (const partia of PARTIE) {
    const glosy = wynikWOkregu[partia];
    sumaGlosow[partia] += glosy;
    tabelka += `<td>${glosy}</td>`;
  }

  tabelka += `<td>${Math.round(podzialMandatow.cenaMandatu)}</td>`;

  const altResztaFn = (glosy) => Math.round((glosy + 1) % alternatywnyPodzialMandatow.cenaMandatu) - 1;

  for (const partia of PARTIE) {
    sumaMandatow[partia] += podzialMandatow[partia] || 0;
    tabelka += `<td>${podzialMandatow[partia] || 0}</td>`;
  }

  for (const partia of PARTIE) {
    const reszta = resztaFn(wynikWOkregu[partia]);
    sumaReszt[partia] += reszta;
    tabelka += `<td>${reszta}</td>`;
  }

  tabelka += `</tr>`;

  // Wiersz alternatywny
  tabelka += `<tr><td colspan="4">Rekomendacja: ${rekomendacje.join(',')}; Mandaty bez przesunięcia ${PRZESUNIETE_GLOSY} głosów:</td>`;
  for (const partia of PARTIE) {
    const glosy = alternatywnyWynik[partia];
    tabelka += `<td>${glosy}</td>`;
  }
  tabelka += `<td>${Math.round(alternatywnyPodzialMandatow.cenaMandatu)}</td>`;
  for (const partia of PARTIE) {
    altSumaMandatow[partia] += alternatywnyPodzialMandatow[partia] || 0;
    let style = '';
    if ((alternatywnyPodzialMandatow[partia] || 0) > (podzialMandatow[partia] || 0)) {
      style = ` style="background-color: green"`;
    } else if ((alternatywnyPodzialMandatow[partia] || 0) < (podzialMandatow[partia] || 0)) {
      style = ` style="background-color: red"`;
    }
    tabelka += `<td ${style}>${alternatywnyPodzialMandatow[partia] || 0}</td>`;
  }
  for (const partia of PARTIE) {
    const reszta = altResztaFn(alternatywnyWynik[partia]);
    altSumaReszt[partia] += altResztaFn(alternatywnyWynik[partia]);
    tabelka += `<td>${reszta}</td>`;
  }
  tabelka += `</tr>`;
});

tabelka += `<tr><td colspan="3">Suma</td>`;
tabelka += `<td>${Object.values(sumaGlosow).reduce((acc, cur) => {
  return (acc += cur);
}, 0)}</td>`;
for (const partia of PARTIE) {
  tabelka += `<td>${sumaGlosow[partia]}</td>`;
}
tabelka += `<td>-</td>`;
for (const partia of PARTIE) {
  tabelka += `<td>${sumaMandatow[partia]}</td>`;
}
for (const partia of PARTIE) {
  tabelka += `<td>${sumaReszt[partia]}</td>`;
}
tabelka += `</tr><tr>`;

tabelka += `<td colspan="10">Alternatywny wynik bez przesunięcia ${sumaPrzesunietych} głosów</td>`;
for (const partia of PARTIE) {
  let style = "";
  if ((sumaMandatow[partia] || 0) > (altSumaMandatow[partia] || 0)) {
    style = ` style="background-color: green"`;
  } else if ((sumaMandatow[partia] || 0) < (altSumaMandatow[partia] || 0)) {
    style = ` style="background-color: red"`;
  }
  tabelka += `<td${style}>${altSumaMandatow[partia]}</td>`;
}
for (const partia of PARTIE) {
  tabelka += `<td>${altSumaReszt[partia]}</td>`;
}
tabelka += `</tr>`;

symulacje.innerHTML = tabelka + "</table>";
document.getElementById("tabela").appendChild(symulacje);


export function RozkladResztWOkregach(wynik: Wynik) {
  const rozkladReszt = {
    PIS: {},
    KO: {},
    TD: {},
    LEWICA: {},
    KONF: {},
  };

  for (let i = 1; i <= ILE_SYMULACJI; i++) {
    const sumaReszt: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
    const sumaMandatow: Wynik = { KO: 0, PIS: 0, LEWICA: 0, TD: 0, KONF: 0 };
    for (const okreg of okregi) {
      const { odchylenieWOkregu } = PoparcieWOkregu(wynik, okreg);
      const wynikWOkregu = WYNIKI_2023[okreg.nr -1 ];
      const procentyWOkreguSrednia = NormalizujWynik(wynikWOkregu);
      const bucketFn = (num, bucket) => Math.round(num * bucket) / bucket;

      // Losowanie poparcia dla partii
      let { procentWOkreguLosoweUncapped, procentWOkreguLosoweCapped } =
        losowanieWyniku(procentyWOkreguSrednia, odchylenieWOkregu, bucketFn);
      // Podział mandatów
      const mandatyWOkregu = PodzialMandatowWOkregu(
        procentWOkreguLosoweUncapped,
        okreg
      );
      const glosy = glosy2019[okreg.nr - 1].reduce(
        (accumulator: number, currentValue: number): number => {
          return accumulator + currentValue;
        },
        0
      );
      const glosyFn = (procent) => Math.round((glosy * procent) / 100);
      const resztaFn = (glosy) =>
        // Math.round(
        //   (glosy % mandatyWOkregu.cenaMandatu)
        // );
        Math.max(
          0,
          (((glosy % mandatyWOkregu.cenaMandatu) + 8) %
            mandatyWOkregu.cenaMandatu) -
            10
        );

      // console.log(procentWOkreguLosoweUncapped, mandatyWOkregu);
      for (const partia of PARTIE) {
        const glosyPartia = glosyFn(procentWOkreguLosoweUncapped[partia]);
        const reszta = resztaFn(glosyPartia);
        // console.log(partia, glosyPartia, reszta);
        sumaReszt[partia] += reszta || 0;
        sumaMandatow[partia] += mandatyWOkregu[partia] || 0;
      }
    }
    // console.log(sumaMandatow, sumaReszt);
    for (const partia of PARTIE) {
      const bucket = `${Math.round(sumaReszt[partia] / 10000) * 10000}`;
      // console.log(rozkladReszt);
      if (rozkladReszt[partia][bucket]) {
        rozkladReszt[partia][bucket] += 1;
      } else {
        rozkladReszt[partia][bucket] = 1;
      }
    }
  } // FOR
  return rozkladReszt;
}

const rozkladReszt = RozkladResztWOkregach(sondaze.PKW_20231017 as Wynik);
console.log(rozkladReszt);
// const rozkladReszt = RESZTY;

for (const partia of PARTIE) {
  rysujHistogram(rozkladReszt[partia], partia);
}

export function rysujHistogram(rozkladReszt: any, partia: string) {
  const canvas = document.createElement("canvas");
  document.getElementById("HistogramReszt" + partia).appendChild(canvas);
  const color = KOLOR_PARTII[partia];

  new Chart(canvas as ChartItem, {
    type: "bar",
    data: {
      labels: Object.keys(rozkladReszt),
      datasets: [
        {
          label: "częstość dla " + partia,
          data: Object.entries(rozkladReszt).map(
            ([bucket, czestosc]) => czestosc
          ),
          backgroundColor: color,
        },
      ],
    },
  });
}
